$closeBtnClr: rgba(241, 182, 182, 0.993);
$blue: #014282;
$orange: #c94c08;

@mixin center{
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin orangeBtn{
    color: white;
    background-color: $orange;
}

@mixin inputs{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
    background: #f2f2f2;
}