@use '../../../globals.scss';

.thirdContentComp{
    display: flex;
    justify-content: center;
    gap: 3em;
    margin: 0 .5em;
    flex-wrap: wrap;
}

.titello{
    text-align: center;
    padding: 1em;
}

.thirdContent{
    border-radius: 10px;
    margin: 2em 0 0 0;
}

.btnContainer{
    padding: 2em;
    display: flex;
    justify-content: center;
}

.learnMoreBtn{
    background-color: globals.$orange;
    color: white;
}

.learnMoreBtn:hover{
    color: whitesmoke;
}

@media screen and (max-width: 800px) {
    .thirdContentComp{
        justify-content: center;
    }
}