.secondContent{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2em;
    margin: 2em auto;
}

@media screen and (max-width: 800px) {
    .secondContent{
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 500px) {
    .secondContent{
        grid-template-columns: 1fr;
    }
}