@use '../../../globals.scss';

.megaMenuContainer{
    position: fixed;
    background-color: #111;
    width: 100%;
    height: 0;
    overflow: hidden;
    transition: height .4s;
    left: 0;
    top: 0;
    border-radius: 50% 0;
    z-index: 2;
}

.megaMenuContainerToggle{
    height: 200px;
}

.title{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid white;
    margin: 0 3em;
}

.title > div{
    margin: 1em;
}

.closeBtn{
    color: white;
    background-color: transparent;
    border: none;
    border-bottom: solid 1px white;
    padding: .5em 2em;
}

.closeBtn:hover{
    color: globals.$closeBtnClr;
    border-bottom: globals.$closeBtnClr 1px solid;
}

.closeBtn:focus{
    outline: none;
}

.megaMenuContent{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 2em;
    gap: 2em;
}

@media screen and (max-width: 1000px) {
    .megaMenuContainerToggle{
        overflow: auto;
    }
}

@media screen and (max-width: 400px) {
    .closeBtn{
        padding: .2em .9em;
    }
}