.menu{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 1em;
    border-bottom: 1px solid white;
    gap: 1em;
}

.linkItem{
    color: rgba(221, 221, 221, 0.671);
    cursor: pointer;
}

.linkItem:hover{
    color: white;
}

.linksContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1em;
}

.titreStyle{
    color: white;
}

@media screen and (max-width: 400px) {
    .menu{
        width: 90%;
    }
}