@use '../../../globals.scss';

.fourthContent{
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ddd;
    background-image: url('../../../../public/assets/hiring.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.itemsInFourthContent{
    a, p, h2{
        z-index: 1;
    }
    width: 80%;
    padding: 3em;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
}

.jobOpBtn{
    background-color: globals.$orange;
    color: white;
}

.jobOpBtn:hover{
    color: white;
}

.blurryBg{
    position: absolute;
    z-index: 0;
    display: block;
    background: #ffffff77;
    width: 100%;
    height: 100%;
    filter: blur(5px);
    top: 0;
}

.workWithUsText{
    z-index: 2;
}

@media screen and (max-width: 400px) {
    .itemsInFourthContent{
        text-align: center;
        padding: 3em 1em;
    }
}