@use '../../globals.scss';

.navbar{
    position: absolute;
    width: 100%;
    background: linear-gradient(to right, transparent, #ffffff3b);
}

.lang{
    @include globals.inputs;
}

.menuIcon > div{
    width: 35px;
    height: 5px;
    background-color: black;
    margin: 6px 0;
}

.logoImg{
    height: 70px;
}

.menuIcon{
    cursor: pointer;
}

@media screen and (max-width: 440px) {
    .navbar{
        position: static;
    }
}