@use '../../../globals.scss';

.firstContentContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2em;
    // background-image: url('../images/imgContent1.jpg');
    // background-position: center;
    // background-repeat: no-repeat;
    // background-size: cover;
}

.cardContainer{
    display: flex;
    justify-content: space-around;
    gap: 2em;
}

.textAndBtn{
    width: 40%;
    margin: 0 auto;
    text-align: center;
    position: absolute;
    top: 30%;
    left: 5%;
    font-size: 1.2em;
    color: white;
}

.contentButImage{
    display: flex;
    flex-direction: column;
    gap: 4em;
    padding: 2em;
}

.backgroundImgContent1{
    width: 100%;
    height: 70vh;
}


@media screen and (max-width: 1000px) {
    .firstContentContainer{
        margin: 0;
    }

    .cardContainer{
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 800px) {
    .textAndBtn{
        width: 90%;
    }

    .contentButImage{
        padding: .1em;
    }
}

@media screen and (max-width: 450px) {
    .textAndBtn{
        top: 50%;
    }
}